@import "partials/file-select";


.question-mark {
	font-size: 4rem;
	font-weight: bold;
	animation: questionmark 1s ease-out infinite;
}

@keyframes questionmark {
	to {
		transform: scale(1.3);
	}
}

.image-preview {

	img {
		width: 300px;
		height: 300px;
		object-fit: cover;
	}
}

